* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: #333;
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  border: 2px solid #333;
  padding: 30px;
  background: url('../public/img/pizza4.jpg') center center / cover no-repeat;
  color: #fff;
}
.state h1 {
  text-align: center;
  margin-bottom: 30px;
}
.state .search {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}
.state input {
  display: block;
  width: 90%;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.8);
  border: 1px solid #999;
  padding-left: 15px;
}
.state button {
  width: 10%;
  height: 40px;
  background-color: rgba(255, 0, 0, 0.5);
  color: #fff;
  border: 1px solid #fff;
  cursor: pointer;
}
.state p {}
.state span {}